    --primary-color: #007bff;
    --secondary-color: #6c757d;
    --background-color: #f8f9fa;
    --text-color: #212529;
    --white: #ffffff;
    --black: #000000;
    --light-gray: #f9f9f9;
    --dark-gray: #e9e9e9;
    --dark-blue: #1f4068;
    --hover-blue: #0056b3;
    --modal-overlay-bg: rgba(0, 0, 0, 0.5);
    --modal-bg: #fff;
    --modal-header-bg: #007bff; /* Changed for better contrast */
    --modal-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    --modal-border-radius: 8px;
    --close-button-color: #aaa;
    --close-button-hover: #000;
    --input-border: #ddd;
    --input-border-focus: #007bff;
    --input-bg: #dedcdc;
    --input-text-color: #000;
}

/* Base Styles */
body, html {
    margin: 0;
    padding: 0;
    height: 100%;
    font-family: Arial, sans-serif;
    background-color: var(--background-color);
    color: var(--text-color);
}

.app-container {
    display: grid;
    grid-template-columns: 1fr 4fr;
    grid-template-rows: min-content auto;
    grid-template-areas: 
        "header header"
        "sidebar main";
    height: 100vh;
}

.header {
    grid-area: header;
    background-color: var(--primary-color);
    display: flex;
    align-items: flex-end;
    justify-content: flex-start;
    font-size: 24px;
    box-shadow: 0 2px 5px rgba(0,0,0,0.2);
    padding: 40px 0px 0px 35px;
    color: var(--white);
}

.header h1 {
    display: flex;
    align-items: center;
    gap: 10px;
    margin: 0;
}

.header i {
    font-size: 1.5em;
}
.sidebar {
    grid-area: sidebar;
    background-color: var(--secondary-color);
    overflow-y: auto;
    padding: 20px;
}

h2 {
    margin: 0;
    font-size: 2.5em;
}

.sidebar button#add-project,
.main-content button#add-todo {
    border-radius: 50%;
    width: 30px;
    height: 30px;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    background-color: var(--primary-color);
    border: none;
    cursor: pointer;
}

.hidden {
    display: none !important;
}

.visible {
    display: flex !important;
    align-items: center;
    justify-content: center;
}

.modal {
    display: none;
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: var(--modal-overlay-bg);
    display: flex;
    align-items: center;
    justify-content: center;
}

.modal-content {
    background-color: var(--modal-bg);
    border-radius: var(--modal-border-radius);
    box-shadow: var(--modal-shadow);
    padding: 20px;
    width: 50%;
    max-width: 600px;
    position: relative;
    animation: slide-down 0.3s ease-out;
}

@keyframes slide-down {
    from {
        transform: translateY(-50px);
        opacity: 0;
    }
    to {
        transform: translateY(0);
        opacity: 1;
    }
}

.modal-header {
    background-color: var(--modal-header-bg);
    padding: 15px;
    border-radius: var(--modal-border-radius) var(--modal-border-radius) 0 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: -20px -20px 20px -20px;
    color: var(--white); /* Ensure the text color is readable */
}

.modal-header h2 {
    margin: 0;
    font-size: 1.5em;
}

.close-button {
    color: var(--close-button-color);
    font-size: 28px;
    font-weight: bold;
    cursor: pointer;
    transition: color 0.2s;
}

.close-button:hover,
.close-button:focus {
    color: var(--close-button-hover);
}

.modal-body {
    display: flex;
    flex-direction: column;
    gap: 20px; /* Increased gap for more spacing */
    padding: 20px;
}

input[type="text"],
input[type="date"],
select,
textarea {
    width: 100%;
    padding: 12px; /* Increased padding for better spacing */
    margin-top: 10px; /* Increased margin for better spacing */
    border: 1px solid var(--input-border);
    border-radius: 4px;
    background-color: var(--input-bg);
    color: var(--input-text-color);
    transition: border-color 0.2s;
}

input[type="text"]:focus,
input[type="date"]:focus,
select:focus,
textarea:focus {
    border-color: var(--input-border-focus);
    outline: none;
}

button {
    padding: 12px; /* Increased padding for consistency */
    border: none;
    border-radius: 4px;
    background-color: var(--primary-color);
    color: var(--white);
    cursor: pointer;
    transition: background-color 0.2s;
}

button:hover {
    background-color: var(--hover-blue);
}

.main-content {
    grid-area: main;
    overflow-y: auto;
    display: flex;
    flex-direction: column;
    padding: 40px 40px;
}

.project-header,
.todo-header {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    padding-bottom: 10px;
    border-bottom: 1px solid var(--primary-color);
    justify-content: space-between;
}

#project-title {
    margin-bottom: 20px;
    text-align: center;
    padding-bottom: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}

.project-actions {
    display: flex;
    gap: 10px;
    justify-content: flex-end;
}

.project-actions button {
    background: none;
    border: none;
    cursor: pointer;
    color: var(--text-color);
    font-size: 1.2em;
}

.project-actions button:hover {
    color: var(--primary-color);
}

.title-with-icon {
    border-bottom: 1px solid var(--primary-color);
    padding-bottom: 10px;
    display: inline-flex;
    align-items: center;
    gap: 10px;
    justify-content: center;
}

.calendar-icon {
    margin-right: 10px;
    font-size: 48px;
    vertical-align: middle;
}

ul {
    list-style: none;
    margin-top: 0;
    padding: 0;
}

li {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: var(--light-gray);
    margin-bottom: 8px;
    padding: 10px 15px;
    border-radius: 4px;
    box-shadow: 0 2px 5px rgba(0,0,0,0.1);
    cursor: pointer;
    transition: background-color 0.3s;
    font-size: 16px;
    color: var(--black);
}

li:hover, 
.task-filters li:hover,
.task-filters li.active {
    background-color: var(--dark-gray);
}

.task-filters li {
    display: flex;
    align-items: center;
    gap: 8px;
    background-color: var(--dark-blue);
}

li.active {
    background-color: var(--primary-color);
}

#todo-list li {
    color: var(--primary-color);
}

#todo-list li.active {
    background-color: var(--primary-color);
    color: var(--white);
}
#project-list li{
    color: var (--primary-color);
    cursor: pointer;
    transition: background-color 0.3s;
}
#project-list li:hover {
    background-color: var(--dark-gray);
}

#project-list li.active {
    background-color: var(--primary-color);
    color: var(--white);
}

button, 
input[type="text"],
input[type="date"], 
select, 
textarea {
    width: 100%;
    padding: 12px; /* Increased padding for consistency */
    margin-top: 10px; /* Increased margin for consistency */
    border: none;
    background-color: var(--input-bg);
    color: var(--input-text-color);
    cursor: pointer;
}

button:hover {
    background-color: var(--hover-blue);
}

/* Todo Item Styles */
.todo-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: var(--light-gray);
    margin-bottom: 8px;
    padding: 10px 15px;
    border-radius: 4px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    cursor: pointer;
    transition: background-color 0.3s;
    color: var(--primary-color);
}

.priority-circle {
    width: 15px;
    height: 15px;
    border-radius: 50%;
    margin-right: 10px;
    position: relative;
    display: inline-block;
    cursor: pointer;
}

.priority-circle input {
    opacity: 0;
    position: absolute;
    width: 100%;
    height: 100%;
    margin: 0;
    cursor: pointer;
}

.priority-circle.low {
    background-color: green;
}

.priority-circle.medium {
    background-color: yellow;
}

.priority-circle.high {
    background-color: red;
}

.todo-item.completed .priority-circle.low {
    background-color: lightgreen;
}

.todo-item.completed .priority-circle.medium {
    background-color: lightyellow;
}

.todo-item.completed .priority-circle.high {
    background-color: pink;
}

.todo-item.completed .todo-title, 
.todo-item.completed .todo-due-date,
.todo-item.completed .todo-description {
    text-decoration: line-through;
    opacity: 0.6;
}

.todo-item:hover {
    background-color: var(--dark-gray);
}

.todo-details {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    margin-left: 10px;
}

.todo-title {
    font-size: 16px;
    font-weight: bold;
}

.todo-due-date {
    font-size: 14px;
    color: var(--secondary-color);
}

.todo-description {
    font-size: 14px;
    color: var(--secondary-color);
    margin-top: 5px;
}

.todo-actions {
    display: flex;
    gap: 10px;
}

.todo-actions button {
    background: none;
    border: none;
    cursor: pointer;
    color: var(--text-color);
    font-size: 1em;
    padding: 3px 5px;
    transition: color 0.3s;
    width: auto;
}

.todo-actions button:hover {
    color: var(--primary-color);
}

@media (max-width: 768px) {
    .app-container {
        grid-template-columns: 1fr;
        grid-template-areas: 
        "header" 
        "sidebar" 
        "main";
    }
}
